@import url("https://fonts.googleapis.com/700&family=Poppins:wght@300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --colour-bg: #1f1f38;
  --colour-bg-variant: #2c2c6c;
  /* -- A bluish colour template...*/
  /* --colour-primary: #4db5ff; */
  /* -- A orange colour template...*/
  --colour-primary: #eead17;

  --colour-primary-header-image: #4db5ff;
  --colour-primary-variant: rgba(77, 181, 255, 0.4);
  --colour-white: #fff;
  --colour-light: rgba(255, 255, 255, 0.6);

  /* Used for media queries */
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 600ms ease;
}

html {
  /* I used in a project earlier and I really liked how it looked */
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  /* I generally dont like scroll bars and tend to remove them if possible */
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: var(--colour-bg);
  color: var(--colour-white);
  line-height: 1.7;
  background-image: url(../src/assets/bg-texture.png);
}

/* ==================== General ==================== */
.container {
  width: var(--container-width-lg);
  margin: 0 auto; /* Centres horizontally */
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  /* Not sure if I need this or not... */
  /* height: 100vh; */
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--colour-light);
}

section > h2 {
  color: var(--colour-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--colour-light);
}

a {
  color: var(--colour-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--colour-white);
}

.btn {
  width: max-content; /* Matches width of content */
  display: inline-block;
  color: var(--colour-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--colour-primary);
  transition: var(--transition);
}

.btn:hover {
  background-color: var(--colour-white);
  color: var(--colour-bg);
  border-color: transparent;
}

.btn-primary {
  background-color: var(--colour-primary);
  color: var(--colour-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* =============== Media queries ===============  */

/* Medium devices */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* =============== Media queries ===============  */

/* Small devices */
@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-bottom: 2rem;
  }
}
