#experience {
  padding-top: 4rem;
}

.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
}

/* Grabs the 2 divs (Frontend & Backend) */
.experience__container > div {
  width: 100%;
  background: var(--colour-bg-variant);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.experience__container > div:hover {
  background: transparent;
  border-color: var(--colour-primary);
  cursor: default;
  transform: scale(1.05);
}

.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--colour-primary);
}

.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
}

.experience__details {
  display: flex;
  gap: 1rem;
}

.experience__details-icon {
  width: 2.5rem;
  height: 2.5rem;
  object-fit: contain;
  border-radius: 0.1rem;
}

/* =============== Media queries ===============  */

/* Medium devices */
@media screen and (max-width: 1024px) {
  .experience__container {
    grid-template-columns: 1fr;
  }

  .experience__container div {
    /* width: 80%; */
    width: 100%;
    padding: 1rem;
    margin: 0 auto;
  }

  .experience__content {
    padding: 1rem;
  }

  .experience__container img {
    margin: auto 0;
  }
}

/* Small devices */
@media screen and (max-width: 600px) {
  .experience__container {
    gap: 1rem;
  }

  .experience__container > div {
    width: 100%;
    padding: 2rem 0;
  }

  .experience__details {
    gap: 0;
  }
}
