#testimonials {
  padding-top: 4rem;
}

.container.testimonials__container {
  width: 40%;
  padding-bottom: 4rem;
}

.client__avatar {
  width: 4rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 0.2rem solid var(--colour-primary);
}

.testimonial {
  background: var(--colour-bg-variant);
  text-align: center;
  padding: 2rem;
  border-radius: 2rem;
  user-select: none;
}

.client__review {
  color: var(--colour-light);
  font-weight: 300;
  display: block;
  width: 80%;
  margin: 0.8rem auto 0;
}

/* Swiper is used for pagination */
.swiper-pagination .swiper-pagination-bullet {
  opacity: 1;
  background-color: var(--colour-light);
}

.swiper-pagination-bullet-active {
  background: var(--colour-primary) !important;
  width: 0.8rem !important;
  height: 0.8rem !important;
}

/* =============== Media queries ===============  */

/* Medium devices */
@media screen and (max-width: 1024px) {
  .container.testimonials__container {
    width: 60%;
  }
}

/* Small devices */
@media screen and (max-width: 600px) {
  .container.testimonials__container {
    width: var(--container-width-sm);
  }

  .client__review {
    width: var(--container-width-sm);
  }
}
